import React from 'react';
import { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col, Modal, Form, Button } from 'react-bootstrap';

import { NavLink } from 'react-router-dom';

//import { useMediaQuery } from 'react-responsive';

import "./HeaderNav.css";
import logo from '../studio.svg';

const HeaderNav = (props) => {

    //const collapsable = useMediaQuery({query: '(max-width: 767px)'});

    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <>
        <Container fluid className="header-nav">
            <Row>
                <Col sm={5} className="d-flex align-items-center justify-content-center logo">
                    <img src={logo} alt="" id="page-logo"></img>
                    <h3 id="studio">STUDIO</h3>
                    <h3 id="medico" className="gradient-text">MEDICO</h3>
                </Col>


                <Col sm={3}>
                    <Row>
                        <Col className="d-flex align-items-center justify-content-center"><NavLink to={{pathname: '/'}} style={{ textDecoration: 'none' }}><h5 className="navlinks">Home</h5></NavLink></Col>
                        <Col className="d-flex align-items-center justify-content-center"><NavLink to={{pathname: '/orari'}} style={{ textDecoration: 'none' }}><h5 className="navlinks">Orari</h5></NavLink></Col>
                        <Col className="d-flex align-items-center justify-content-center"><NavLink to={{pathname: '/contatti'}} style={{ textDecoration: 'none' }}><h5 className="navlinks">Contatti</h5></NavLink></Col>
                    </Row>
                </Col>

                <Col>
                    <Row>
                    <Col><h3 className="d-flex align-items-center justify-content-center signature" onClick={handleShow}>Dott.ssa Ceo Grazia</h3></Col>
                    </Row>
                </Col>

                {/*collapsable ? 
                <>
                </> 
                :
                <>
                <Col md={1} className="d-flex align-items-center justify-content-center"><NavLink to={{pathname: '/'}} style={{ textDecoration: 'none' }}><h5 className="navlinks">Home</h5></NavLink></Col>
                <Col md={1} className="d-flex align-items-center justify-content-center"><NavLink to={{pathname: '/orari'}} style={{ textDecoration: 'none' }}><h5 className="navlinks">Orari</h5></NavLink></Col>
                <Col md={1} className="d-flex align-items-center justify-content-center"><NavLink to={{pathname: '/contatti'}} style={{ textDecoration: 'none' }}><h5 className="navlinks">Contatti</h5></NavLink></Col>
                
                </>*/}
                    
                {/*<Col><h3 className="d-flex align-items-center justify-content-center signature" onClick={handleShow}>Dott.ssa Ceo Grazia</h3></Col>*/}
            </Row>
        </Container>

        <ModalLogIn show={show} handleClose={handleClose} auth={props.auth} user={props.user}/>
        </>
    );
};

const ModalLogIn = (props) => {

    const [email, setEmail] = useState('');
    const [passwd, setPasswd] = useState('');

    const [failed, setFailed] = useState(false);

    const signIn = (e) => {
        e.preventDefault();
        
        props.auth.signInWithEmailAndPassword(email.trim(), passwd) //'studiomedico.ceo@libero.it', 'Mazzini92'
            .then( () => handleClose())
            .catch(() => { setEmail(''); setPasswd(''); setFailed(true); console.log('failed login'); });

        // per il sign out basta chiamare auth.signOut();
    };

    const handleClose = () => {
        setEmail('');
        setPasswd('');
        setFailed(false);
        props.handleClose();
    }

    return (
        <Modal show={props.show} onHide={handleClose} backdrop='static'>
            <Modal.Header className="modal-full">
                <Modal.Title className="modal-title gradient-text ">Log {props.user ? 'out?' : 'in'}</Modal.Title>
            </Modal.Header>
        {
            props.user ? <Modal.Footer className="modal-foot"><Button onClick={handleClose} className="modal-text close-btn">Chiudi</Button><Button className="modal-text submit-btn" variant='primary' onClick={() => props.auth.signOut()}>Log out</Button></Modal.Footer> :
            <Form onSubmit={signIn} >
            <Modal.Body className="modal-full">
                <Form.Group as={Row} controlId="formPlaintextEmail">
                    <Form.Label column sm="2" className="modal-text">
                        Email
                    </Form.Label>
                    <Col sm="10">
                        <Form.Control className="round-input-box" type="text" value={email} onChange={e => setEmail(e.target.value)} required/>
                        { failed && <div style={{color: "#ff3366"}}>Email o password errati</div> }
                    </Col>
                </Form.Group>

                <Form.Group as={Row} controlId="formPlaintextPassword">
                    <Form.Label column sm="2" className="modal-text">
                        Password
                    </Form.Label>
                    <Col sm="10">
                        <Form.Control className="round-input-box" type="password" placeholder="Password" value={passwd} onChange={e => setPasswd(e.target.value)} required/>
                    </Col>
                    </Form.Group>
            </Modal.Body>

            <Modal.Footer className="modal-foot">
                <Button onClick={handleClose} className="modal-text close-btn">
                    Chiudi
                </Button>
                <Button type="submit" className="modal-text submit-btn" >
                    Log in
                </Button>
            </Modal.Footer>
            </Form>
        }
        </Modal>
    );
};

export default HeaderNav;
