
import HeaderNav from './components/HeaderNav.js';
import Avvisi from './components/Avvisi.js';
import Orari from './components/Orari.js';
import Contatti from './components/Contatti.js';

import './App.css';

import firebase from 'firebase/app';
import 'firebase/firestore';
import 'firebase/auth';

import { useAuthState } from 'react-firebase-hooks/auth';

import { BrowserRouter as Router, Redirect, Route, Switch } from 'react-router-dom';

firebase.initializeApp({
    apiKey: "AIzaSyDmjGsK5060pPJhOapJ8ioCVf_l3-zLMGA",
    authDomain: "studio-annunci.firebaseapp.com",
    projectId: "studio-annunci",
    storageBucket: "studio-annunci.appspot.com",
    messagingSenderId: "461022172426",
    appId: "1:461022172426:web:de51657a1acbaf28a716f3"
});

const auth = firebase.auth();
const firestore = firebase.firestore();

const App = () => {

    const [user] = useAuthState(auth);

    return (
        <Router basename={process.env.PUBLIC_URL}>
            <body>
                <HeaderNav user={user} auth={auth} />

                <Switch>
                    <Route exact path='/' render={() => <Avvisi user={user} auth={auth} firestore={firestore} />} />
                    <Route exact path='/orari' render={() => <Orari />} />
                    <Route exact path='/contatti' render={() => <Contatti />} />
                    <Route><Redirect to='/' /></Route>
                </Switch>
                
            </body>
        </Router>
    );
};

export default App;